import common from '../../../common/common.js'

export default {
	data() {
		return {
			userid:'',
			userInfo:[],
			addressList:[],
			tfrom:'',		//来自
			info:{},
			
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
		};
	},
	onLoad(option) {
		var that = this
		var userid = uni.getStorageSync('userid');
		var userInfo = uni.getStorageSync('userInfo');
		var tfrom = option.from;
		if(tfrom != '' || tfrom != 'undefined'){
			that.tfrom = tfrom
		}
		
		that.userid = userid
		that.userInfo = userInfo
		if(userInfo == ''){
			uni.redirectTo({
				url: '/pages/userlogin/userlogin'
			});
		}
		
		var currentPages = getCurrentPages()
		that.info.delta = currentPages.length
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
		
		that.getAddressList();
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');	
		var userid = uni.getStorageSync('userid');
		var userInfo = uni.getStorageSync('userInfo');
		that.userid = userid
		that.userInfo = userInfo
		that.getAddressList();
		
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	
	methods:{
		//选择地址
		chooseaddress(e){
			var that = this
			var id = e.currentTarget.dataset.id
			var shopid = uni.getStorageSync('shopid');
			console.log("id",id);
			uni.request({
				url: common.apiUrl+'user/getAddress',
				method: 'GET',
				data: {
					id:id,
					shopid:shopid
				},
				success: res => {
					var data = res.data
					console.log("data",data);
					if(data.code == 0){
						
						if(data.address != ''){
							
							if(data.isused == 1){
								uni.showToast({
									title: '此地址不在配送范围内',
									duration: 1000,
									icon :'none'
								})
								return false
							}else{
								var address = data.address;
								common.address = address;
								common.isused = data.isused
								//uni.setStorageSync('address',address);
								uni.navigateBack();
							}
							
						}
						
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//编辑地址
		editaddress(e){
			var that = this
			var id = e.currentTarget.dataset.id
			uni.navigateTo({
				url: '/pages/user/setting/editaddress?id='+id,
				success: res => {
					
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//设置默认地址
		setdefault(e){
			let that = this
			var id = e.currentTarget.dataset.id
			var isdefault = e.currentTarget.dataset.isdefault
			if(isdefault == 1){
				return false;
			}
			var userInfo = uni.getStorageSync('userInfo');
			uni.showModal({
				title: '提示',
				content: '确定要设为默认地址吗？',
				success: res => {
					uni.request({
						url: common.apiUrl+'user/setdefault',
						method: 'GET',					
						data: {
							id:id,
							userid:userInfo.users_id
						},
						success: res => {
							if(res.data.code == 0){
								uni.showToast({
									title: '操作成功',
									duration: 1500,
									icon:'success'
								});
								that.getAddressList();
							}
						},
					});
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//删除地址
		deladdress(e){
			let that = this
			var id = e.currentTarget.dataset.id
			uni.showModal({
				title: '提示',
				content: '确定要删除地址吗？',
				success: res => {
					if (res.confirm) {
						uni.request({
							url: common.apiUrl+'user/deladdress',
							method: 'GET',					
							data: {
								id:id
							},
							success: res => {
								if(res.data.code == 0){
									uni.showToast({
										title: '操作成功',
										duration: 1500,
										icon:'success'
									});
									that.getAddressList();
								}
							},
						});
					}
					
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//新增地址
		addaddress(){
			var that = this
			uni.navigateTo({
				url: '/pages/user/setting/editaddress',
				success: res => {
					
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//地址列表
		getAddressList(){
			var that = this
			var userid = uni.getStorageSync('userid');
			uni.request({
				url: common.apiUrl+'location/getAddressList',
				method: 'GET',
				data: {
					userid:userid
				},
				success: res => {
					console.log("addressList",that.addressList);
					if(res.data.code == 0){
						that.addressList = res.data.address_list
						
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
